/**
 * Data (Label with values)
 */
export interface SrvData {
  ue: string;
  data: Value[];
}

export interface NbrData {
  ue: string;
  data: CellList[];
}

/**
 * Value
 */
export interface Value {
  x: Date;
  y: number;
}

export interface Cell {
  arfcn: number;
  pci: number;
}

export interface CellList {
  time: Date;
  nbrCells: Cell[];
  //measRslt: MeasRslt;
}

export interface MeasRslt {
  rsrp: number;
  rsrq: number;
  sinr: number;
}

