  /**
   * Update datasets
   */
  private updateDatasets() {
    //serving data
    this.srvData.forEach(data => {
      data.data.push({
        x: new Date(),
        y: Math.random() * 1000
      });
      if (data.data.length > 30) {
        data.data.splice(0, 1);
      }
    });

    const dataset = (labelData: SrvData) => ({
      type: "line",
      label: labelData.ue,
      data: labelData.data,
      radius: 10,
      showLine: true,
      fill: true
      //borderColor:"rgba(255,99,132,0.2)"
    });

    this.srvChartData = {
      datasets: this.srvData.map(label => dataset(label))
    };

    //neighbor data
    const cells: Cell[] = [];

    //generate 5 nbr cells
    for (let i = 0; i < 5; i++) {
      const cell: Cell = {
        arfcn: Math.floor(Math.random() * 50),
        pci: Math.floor(Math.random() * 10)
      };
      cells.push(cell);
    }

    this.nbrData.forEach(data => {
      data.data.push({
        time: new Date(),
        nbrCells: cells
      });
      if (data.data.length > 30) {
        data.data.splice(0, 1);
      }
    });

    //console.log(this.nbrData);
    const nbrdataset = (labelData: NbrData) => ({
      type: "bar",
      label: labelData.ue,
      data: labelData.data[labelData.data.length - 1].nbrCells.map(
        cell => cell.pci
      ),
      radius: 10,
      showLine: true,
      fill: true
      //borderColor:"rgba(255,99,132,0.2)"
    });

    this.nbrChartData = {
      labels: this.labelss, //this.nbrData.map(label => nbrlabel(label)),//['1','2','3','4','5'],//
      datasets: this.nbrData.map(label => nbrdataset(label))
    };
  }
