
import {
  Component,
  Prop,
  Vue,
  Watch,
  Mixins,
  Emit
} from "vue-property-decorator";
import Chart from "chart.js";
import VueChart from "vue-chartjs";
import "chartjs-plugin-colorschemes";
import "chartjs-plugin-zoom";
import "chartjs-plugin-crosshair";

export interface SrvData {
  ue: string;
  data: Value[];
}

/**
 * Value
 */
export interface Value {
  x: Date;
  y: number;
}



export interface NbrData {
  ue: string;
  data: NbrValue[];
}

export interface NbrValue {
  x: Date;
  y: number;
}

export interface Cell {
  arfcn: number;
  pci: number;
}

export interface CellList {
  time: Date;
  nbrCells: Cell[];
  //measRslt: MeasRslt;
}

export interface MeasRslt {
  rsrp: number;
  rsrq: number;
  sinr: number;
}

/**
 * Time serese chart
 */
@Component
export default class TimeSeriesChart extends Mixins(
  VueChart.Line,
  VueChart.mixins.reactiveProp
) {

  private srvData: SrvData[] = [];  
  private nbrData: NbrData[] = []; 

  private srvChartData: Chart.ChartData = {};
  private nbrChartData: Chart.ChartData = {};
  private allData: Chart.ChartData = {datasets:[]};
  /**
   * Chart data
   */
  @Prop({ required: true, default: {} })
  public chartData!: Chart.ChartData;

  /**
   * Reset chart
   */
  @Prop({ default: false })
  public resetChart!: boolean;

  /**
   * Chart options(If override default options)
   */
  @Prop()
  public chartOptions!: Chart.ChartOptions;

  /**
   * Chart options
   */
  private options: Chart.ChartOptions = {};

  /**
   * Chart instance
   */
  private chart!: Chart;

  /**
   * Chart instance for handling zoom actions
   */
  private zoomChartInstance!: Chart;

  /**
   * initialize
   */
  mounted() {
    console.log('tchart onmount 1');
    if (!this.chartOptions) {
      this.applyDefaultOptions();
    }
    this.updateSrvData();
    this.updateNbrData();
    //console.log(this.srvChartData);
    //console.log(this.srvChartData.datasets[0]);
    //

    if (this.allData?.datasets && this.srvChartData?.datasets)
    {
      console.log("haha1");
      this.allData.datasets.push(this.srvChartData.datasets[0]);
    }
    
    if (this.allData?.datasets && this.nbrChartData?.datasets)
    {
      console.log("haha2");
      this.allData.datasets.push(this.nbrChartData.datasets[0]);
    }
    
    console.log(this.allData);
    this.renderChart(this.allData, this.options);
    //this.renderChart(this.nbrChartData, this.options);
    // Draw chart
    //this.renderChart(this.chartData, this.options);
    this.chart = this.$data._chart;
    //console.log(this.chart);
    console.log('tchart onmount 2');
  }

  private updateSrvData() {
    
    for (let i = 1; i <= 1; i++) {
      this.srvData.push({ ue: "UE(srv)" + i, data: [] });
      //this.nbrData.push({ ue: "UE(nbr)" + i, data: [] });
    }  
    
    const currentDate = new Date("2020-01-15T21:48:50.746674");

    for (let i = 1; i <= 100; i++) {
      this.srvData.forEach(data => {
        data.data.push({
          x: new Date(currentDate.getTime() + 300 * i),
          y: Math.floor(90 + Math.random() * 5)
        });
      });
    }

    //const labelset = (labelData: SrvData) => labelData.data.x;

    const dataset = (labelData: SrvData) => ({
      type: "line",
      label: labelData.ue,
      data: labelData.data,
      radius: 4,
      showLine: true,
      fill: true
      //borderColor:"rgba(255,99,132,0.2)"
    });

    this.srvChartData = {
      datasets: this.srvData.map(label => dataset(label))
    };
  }

  private updateNbrData() {
    
    for (let i = 1; i <= 1; i++) {
      this.nbrData.push({ ue: "UE(nbr)" + i, data: [] });
    }  
    
    const currentDate = new Date("2020-01-15T21:48:50.746674");

    for (let i = 1; i <= 100; i++) {
      this.nbrData.forEach(data => {
        data.data.push({
          x: new Date(currentDate.getTime() + 300 * i),
          y: Math.floor(1 + Math.random() * 5)
        });
      });
    }

    //const labelset = (labelData: SrvData) => labelData.data.x;

    const dataset = (labelData: NbrData) => ({
      type: "bar",
      label: labelData.ue,
      data: labelData.data,
      radius: 4,
      showLine: true,
      fill: true
      //borderColor:"rgba(255,99,132,0.2)"
    });

    this.nbrChartData = {
      datasets: this.nbrData.map(label => dataset(label))
    };
  }
/*   updated() {
    console.log('tchart updated 1');
    this.chart = this.$data._chart;
    console.log(this.chart);
  } */

  /**
   * Default Options
   */
  private applyDefaultOptions() {
    this.options.onClick = this.onClick;
    this.options.maintainAspectRatio = false;
    //this.options.indexAxis: 'y';
    (this.options.showLines = true),
      (this.options.scales = {
        /** 
      ticks: {
       major:{
        enabled: true
       }
      },*/
        gridLines: {
          display: false,
          drawBorder: false,
          drawOnChartArea: true,
          drawTicks: false
        },
        xAxes: [
          {
            type: "time",
            time: {
              unit: "second",
              unitStepSize: 1,
              stepSize: 1,
              displayFormats: {
                second: "HH:mm:ss"
              },
              tooltipFormat: "HH:mm:ss.SSS"
            }
          }
        ],
        yAxes: [
          {
            stacked: true
          }
        ]
      });

    this.options.plugins = {
      crosshair: {
        line: {
          color: '#F00',        // crosshair line color
          width: 1,             // crosshair line width
          dashPattern: [0, 0]   // crosshair line dash pattern
        },
        sync: {
          enabled: true,            // enable trace line syncing with other charts
          group: 1,                 // chart group
          suppressTooltips: false   // suppress tooltips when showing a synced tracer
        },
        zoom: {
          enabled: true,                                      // enable zooming
          zoomboxBackgroundColor: 'rgba(66,133,244,0.2)',     // background color of zoom box 
          zoomboxBorderColor: '#48F',                         // border color of zoom box
          zoomButtonText: 'Reset Zoom',                       // reset zoom button text
          zoomButtonClass: 'reset-zoom',                      // reset zoom button class
        },
        callbacks: {
          beforeZoom: function(start: any, end: any) {                  // called before zoom, return false to prevent zoom
            return true;
          },
          afterZoom: function(start: any, end: any) {                   // called after zoom
          }
        }
      },
      colorschemes: {
        scheme: "brewer.Paired12"
      },
     /*  zoom: {
        pan: {
          enabled: true,
          mode: "xy",
          speed: 20,
          threshold: 10,
          onPan: (event: any) => {
            this.zoomChartInstance = event.chart;
          },
          onPanComplete: (event: any) => {
            this.zoomChartInstance = event.chart;
          }
        },

        zoom: {
          enabled: true,
          drag: false,
          mode: (event: any) => {
            return "xy";
          },
          speed: 0.1,
          sensitivity: 3,
          onZoom: (event: any) => {
            //console.log(event);
            this.zoomChartInstance = event.chart;
          },
          onZoomComplete: (event: any) => {
            this.zoomChartInstance = event.chart;
          }
        }
      } */
    };
  }

  /**
   * onClick
   */
  private onClick(
    event?: MouseEvent | undefined,
    activeElements?: {}[] | undefined
  ): any {
    //this.$emit('click-chart-element');
    const element = this.chart.getElementAtEvent(event);
    //console.log(element);
    //console.log(this.chart);
    if (element && element.length > 0) {
      //this.clickChartElement(element);
      this.$emit('click-chart-element', element);
    }
  }

  /**
   * Reset chart position
   */
  @Watch("resetChart")
  private resetChartPosition() {
    //console.log(this.chart);
    if (this.resetChart && this.zoomChartInstance) {
      const chart: any = this.zoomChartInstance;
      chart.resetZoom();
    }
  }

  /**
   * click a chart element
   */
  @Emit("click-chart-element")
  public clickChartElement(element: any) {
    //console.log(element);
    return element;
  }
}
